  padding-bottom: 10px;
}
#profil img{
  padding: 3px;
  border: 1px solid LightSlateGray;
  border-radius: 5px;
}
#profil button{
  height: 20px;
  width: 20px;
  font-size: 10px;
  padding: 0;
}
