      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  margin-top: 10px;
}
.amis{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
